import { Fragment, useEffect, useState } from "react";
import getTime from "../../helpers/time.js";

const DataTime = (props) => {
	const { data, id } = props;
	return (
		<div id={id} className="time-data">
			{data}
		</div>
	);
};

export default function Minutos() {
	const [day, time] = getTime();
	const [minutos, setMinutos] = useState(time.getMinutes());
	const [hours, setHours] = useState(time.getHours());
	const [seconds, setSeconds] = useState(time.getSeconds());
	const [days, setDays] = useState(day);

	const changeTime = async (day, time) => {
		const interData = setTimeout(
			(day, time) => {
				setSeconds(time.getSeconds());
				setMinutos(time.getMinutes());
				setHours(time.getHours());
				setDays(day);
				clearTimeout(interData);
			},
			1000,
			day,
			time
		);
	};

	useEffect(() => {
		const [day, time] = getTime();
		console.log('a');
		const inter = changeTime(day, time);
		return () => {
			clearTimeout(inter);
		};
	}, []);

	useEffect(() => {
		const [day, time] = getTime();
		changeTime(day, time);
	});

	return (
		<Fragment>
			<div className="data-day data">
				<DataTime id="dias" data={days}></DataTime>
				<div className="upp-side"></div>
				<div className="low-side"></div>
				<p>Dias</p>
				<div className="circle"></div>
				<div className="circle circle-2"> </div>
			</div>

			<div className="data-day data">
				<DataTime id="Hours" data={hours}></DataTime>
				<div className="upp-side"></div>
				<div className="low-side"></div>
				<p>Horas</p>
				<div className="circle"></div>
				<div className="circle circle-2"> </div>
			</div>

			<div className="data-day data">
				<DataTime id="minutes" data={minutos}></DataTime>
				<div className="upp-side"></div>
				<div className="low-side"></div>
				<p>Minutos</p>
				<div className="circle"></div>
				<div className="circle circle-2"> </div>
			</div>

			<div className="data-day data">
				<DataTime id="seconds" data={seconds}></DataTime>
				<div className="upp-side"></div>
				<div className="low-side"></div>
				<div id="like_button_container"></div>
				<p>Segundos</p>
				<div className="circle"></div>
				<div className="circle circle-2"> </div>
			</div>
		</Fragment>
	);
}
